methods: {
changeHeader(index) {
console.log(index, 'index')
},
filesAdded(files) {
let hasIgnore = false
const maxSize = 1 * 1024 * 1024 
for (let k in files) {
const file = files[k]
if (file.size > maxSize) {
file.ignore = true
hasIgnore = true
}
}
hasIgnore && this.$createToast({
type: 'warn',
time: 1000,
txt: 'You selected >1M files'
}).show()
}
}
